/* eslint-disable @ngrx/no-store-subscription */
import { INicknameData } from '@ApiClient/backend/Common/nickname';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { default as IUserPictureService } from '@ApiClient/services/userPicture';
import { CommonModule } from '@angular/common';
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import { selectUserIdByPostId } from '@store/feed/feed.selectors';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-usrsimple-card',
  standalone: true,
  imports: [ImageComponent, CommonModule],
  templateUrl: './usrsimple-card.component.html',
  styleUrl: './usrsimple-card.component.scss',
})
export class UsrsimpleCardComponent implements OnInit, OnDestroy {
  @Input() username!: INicknameData | undefined;
  @Input() postId!: string;
  @Input() isEditPage: boolean = false;
  @ViewChild('photoProfile') profilePhoto!: ImageComponent;
  idPicture$!: Subject<string | null>;
  userId$!: Subject<string | null>;
  isPrivateUser: boolean = false;

  idPictureSubject = new Subject<string | undefined>();
  private destroy$ = new Subject<void>();

  constructor(private store: Store) {}

  ngOnInit(): void {
    if (!this.username) {
      this.username = {
        nicknameDisplayed: 'Private User',
        nicknameUrl: 'private_user',
      };
      this.isPrivateUser = true;
    } else {
      this.isPrivateUser = false;
      if (this.username?.nicknameDisplayed != 'AutoSeeder') {
        this.store.select(selectUserIdByPostId(this.postId)).subscribe((userId) => {
          IUserPictureService.getByUserId(userId).subscribe((userPicture) => {
            this.profilePhoto.idPicture = userPicture.data?.fileId ?? null;
            this.profilePhoto.userId = userId ?? null;
            this.profilePhoto.reset();
          });
          this.userId$.next(userId ?? '');
        });
      }
    }
  }

  ngOnDestroy(): void {
    this.destroy$.complete();
  }
}
