/* eslint-disable @ngrx/no-store-subscription */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @ngrx/select-style */
/* eslint-disable @ngrx/prefer-selector-in-select */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @ngrx/avoid-dispatching-multiple-actions-sequentially */
import { Component, Input, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CardModule } from 'primeng/card';
import { SliderModule } from 'primeng/slider';
import { Store, select } from '@ngrx/store';
import { RatingBarComponent } from './rating-bar/rating-bar.component';
import { ButtonModule } from 'primeng/button';
import { IAverage, IAvgType, ISection, RateStatus } from '@store/feed/post/rating/ratingState.interface';
import { Observable, Subject, take, takeUntil } from 'rxjs';
import {
  selectCanVoteByArtworkId,
  selectGlobalArtworkAvgStatusById,
  selectRatingByArtworkId,
  selectRatingStatusByArtworkId,
  selectSoloRatingSessionId,
} from '@store/feed/post/rating/rating.selectors';
import * as ratingActions from '@store/feed/post/rating/rating.actions';
import { ISectionRate, ISectionRatesData } from '@ApiClient/backend/Post/sectionRates';
import { ArtworkSectionTypes } from '@ApiClient/backend/Types/artworkSectionType';
import { SkeletonModule } from 'primeng/skeleton';

@Component({
  selector: 'app-rating',
  standalone: true,
  imports: [CommonModule, CardModule, SliderModule, RatingBarComponent, ButtonModule, SkeletonModule],
  templateUrl: './rating.component.html',
  styleUrl: './rating.component.scss',
})
export class RatingComponent implements OnInit, OnDestroy {
  @ViewChildren(RatingBarComponent) ratingBars!: QueryList<RatingBarComponent>;
  @Input() artworkId!: string;
  rating$!: Observable<ISection | undefined>;
  globalAverage: number | null = null;
  rateSessionId: string = '';
  canVote$!: Observable<boolean | undefined>;
  canVote: boolean = false;
  ratingStatus$!: Observable<string | undefined>;
  private unsubscribe$ = new Subject<void>();
  constructor(private store: Store) {}

  ngOnInit(): void {
    this.ratingStatus$ = this.store.select(selectRatingStatusByArtworkId, { artworkId: this.artworkId });
    this.canVote$ = this.store.select(selectCanVoteByArtworkId, { artworkId: this.artworkId });
    this.rating$ = this.store.select(selectRatingByArtworkId, { artworkId: this.artworkId });
    this.rating$.pipe(takeUntil(this.unsubscribe$)).subscribe((rating) => {
      if (this.ratingBars) {
        if (rating?.canVote != undefined) {
          this.ratingBars.toArray().forEach((ratingBar) => {
            this.canVote = rating?.canVote;
            ratingBar.updateIsVoted(rating?.canVote);
          });
        }
      }
    });
  }

  async onSubmitVote() {
    let dataSend!: ISectionRatesData;
    const sectionRate: ISectionRate[] = [];
    this.ratingBars.forEach((ratingBar) => {
      switch (ratingBar.sectionName) {
        case ArtworkSectionTypes.ArtistCareer:
          sectionRate.push({ sectionName: ArtworkSectionTypes.ArtistCareer, rate: ratingBar.sectionUsrValue ?? 0 });
          break;

        case ArtworkSectionTypes.HistoricalImportance:
          sectionRate.push({ sectionName: ArtworkSectionTypes.HistoricalImportance, rate: ratingBar.sectionUsrValue ?? 0 });
          break;

        case ArtworkSectionTypes.PersonalOpinion:
          sectionRate.push({ sectionName: ArtworkSectionTypes.PersonalOpinion, rate: ratingBar.sectionUsrValue ?? 0 });
          break;

        case ArtworkSectionTypes.StateOfConservation:
          sectionRate.push({ sectionName: ArtworkSectionTypes.StateOfConservation, rate: ratingBar.sectionUsrValue ?? 0 });
          break;

        case ArtworkSectionTypes.TechnicalOpinion:
          sectionRate.push({ sectionName: ArtworkSectionTypes.TechnicalOpinion, rate: ratingBar.sectionUsrValue ?? 0 });
          break;
      }

      dataSend = {
        artworkId: this.artworkId,
        sectionRates: sectionRate,
      };

      this.store
        .pipe(select(selectSoloRatingSessionId, { artworkId: this.artworkId }))
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe((resultId) => {
          this.rateSessionId = resultId;
        });
    });

    this.store.select(selectGlobalArtworkAvgStatusById, { artworkId: this.artworkId }).subscribe((rating) => {
      this.globalAverage = rating?.value ?? null;
    });

    this.store.dispatch(
      ratingActions.postRating({
        data: dataSend,
        rateSessionId: this.rateSessionId,
        firstVoteValues: await this.firstVote(dataSend),
        globalValue: this.globalAverage,
      }),
    );

    this.store
      .select(selectRatingByArtworkId, { artworkId: this.artworkId })
      .pipe(take(1))
      .subscribe((rating) => {
        if (rating?.canVote != undefined) {
          this.ratingBars.toArray().forEach((ratingBar) => {
            ratingBar.updateIsVoted(rating?.canVote);
          });
        }
      });
  }

  isVoteDone() {
    this.ratingBars.forEach((ratingBar) => {
      ratingBar.updateIsVoted(false);
    });
  }

  async firstVote(dataSend: ISectionRatesData): Promise<IAverage[]> {
    const result: IAverage[] = [];
    this.store
      .select(selectGlobalArtworkAvgStatusById, { artworkId: this.artworkId })
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((rating) => {
        if (rating?.value == null || rating?.value == undefined) {
          Object.values(dataSend.sectionRates).forEach((section) => {
            result.push({ value: section.rate ?? null, type: IAvgType.BarRating, status: RateStatus.Done });
          });
        }
      });
    return result;
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }
}
