import { CommonModule } from '@angular/common';
import { ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core';
import { AvatarModule } from 'primeng/avatar';
import { AvatarGroupModule } from 'primeng/avatargroup';
import { SliderModule, Slider } from 'primeng/slider';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Store } from '@ngrx/store';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { SkeletonModule } from 'primeng/skeleton';
import { Observable } from 'rxjs';
import { selectStatusById } from '@store/feed/post/rating/rating.selectors';

@Component({
  selector: 'app-rating-bar',
  standalone: true,
  imports: [CommonModule, SliderModule, AvatarModule, AvatarGroupModule, ReactiveFormsModule, FormsModule, ImageComponent, SkeletonModule],
  templateUrl: './rating-bar.component.html',
  styleUrl: './rating-bar.component.scss',
})
export class RatingBarComponent implements OnInit {
  @Input() sectionName!: string;
  @Input() sectionUsrValue!: number | null;
  @Input() sectionGlobalValue!: number | null;
  @Input() artworkId!: string;
  ratingValue: number = 0;
  modified!: boolean;
  @Input() canVote!: boolean;
  average: number | null = 50;
  @ViewChild('sliderRef') sliderRef!: Slider;
  globalBarValueStatus$!: Observable<string | undefined>;
  userValueStatus$!: Observable<string | undefined>;

  constructor(
    public store: Store,
    private cdr: ChangeDetectorRef,
  ) {}

  ngOnInit(): void {
    this.sectionUsrValue = this.sectionUsrValue ? this.sectionUsrValue : 0;
    this.globalBarValueStatus$ = this.store.select(selectStatusById, {
      artworkId: this.artworkId,
      isGlobalValue: true,
      sectionName: this.sectionName,
    });
  }

  input(event: Event) {
    if (event.target instanceof HTMLInputElement) {
      this.ratingValue = parseInt(event.target.value, 10);
      this.modified = true;
    }
  }

  updateIsVoted(newValue: boolean): void {
    this.sliderRef.setDisabledState(newValue);
    this.cdr.detectChanges();
  }
}
