import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PostComponent } from '@modules/post/post.component';
import { Store } from '@ngrx/store';
import { ActivatedRoute } from '@angular/router';
import * as fromPostActions from '@store/feed/feed.actions';
import * as fromRatingActions from '@store/feed/post/rating/rating.actions';
import { Observable } from 'rxjs';
import { selectOnlyArtworkIds } from '@store/feed/feed.selectors';

@Component({
  selector: 'app-uniquepost',
  standalone: true,
  imports: [CommonModule, PostComponent],
  templateUrl: './uniquepost.component.html',
  styleUrl: './uniquepost.component.scss',
})
export class UniquepostComponent implements OnInit {
  postId!: string;
  onlyArtworkIds$: Observable<(string | undefined)[]> = this.store.select(selectOnlyArtworkIds);

  constructor(
    private route: ActivatedRoute,
    public store: Store,
  ) {
    this.route.params.subscribe((params) => {
      this.postId = params['id'];
    });
  }

  ngOnInit(): void {
    this.store.dispatch(fromPostActions.initUniqueFeedSoloPost({ postId: this.postId }));
    this.onlyArtworkIds$.subscribe((artworkIds) => {
      const filteredArtworkIds = artworkIds.filter((id) => id !== undefined) as string[];
      if (filteredArtworkIds.length >= 1) {
        this.store.dispatch(fromRatingActions.initRating({ postsIds: filteredArtworkIds }));
      }
    });
  }
}
