/* eslint-disable @ngrx/avoid-dispatching-multiple-actions-sequentially */
/* eslint-disable @ngrx/prefer-selector-in-select */
import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscription, finalize, of } from 'rxjs';
import { selectOnlyArtworkIds, selectPostIds } from '@store/feed/feed.selectors';
import * as fromPostActions from '@store/feed/feed.actions';
import * as fromRatingActions from '@store/feed/post/rating/rating.actions';
import { IPostFull } from '@ApiClient/backend/Post/post';
import { AppStateInterface } from '@store/app/app-state.interface';
import { ILoginInfo } from '@ApiClient/backend/Login/loginInfo';
import { selectUserLoginInfo } from '@store/profile/profile.selectors';

@Component({
  selector: 'app-home',
  standalone: false,
  templateUrl: './home.component.html',
  styleUrl: './home.component.scss',
})
export class HomeComponent implements OnInit, OnDestroy {
  postIds$: Observable<string[]> = this.store.select(selectPostIds);
  onlyArtworkIds$: Observable<(string | undefined)[]> = this.store.select(selectOnlyArtworkIds);
  postsItems$!: Observable<IPostFull[]>;
  loading$!: Observable<boolean>;
  userLoginInfo$: Observable<ILoginInfo | null> = this.store.select(selectUserLoginInfo);
  loadingMorePosts: boolean = false;

  private subscription: Subscription = new Subscription();

  constructor(private store: Store<AppStateInterface>) {}

  async ngOnInit() {
    this.store.dispatch(fromPostActions.loadFeedPosts());
    this.onlyArtworkIds$.subscribe((artworkIds) => {
      const filteredArtworkIds = artworkIds.filter((id) => id !== undefined) as string[];
      if (filteredArtworkIds.length >= 1) {
        this.store.dispatch(fromRatingActions.initRating({ postsIds: filteredArtworkIds }));
      }
    });
  }

  ngOnDestroy(): void {
    window.removeEventListener('scroll', this.onScroll, true);
    this.subscription.unsubscribe();
  }

  private isNearBottom(): boolean {
    const tolerance = 150; // Puedes ajustar este valor
    const container = document.getElementById('homeContainer');
    if (!container) return false;
    return window.innerHeight + window.scrollY >= container.offsetHeight - tolerance;
  }

  @HostListener('window:scroll', ['$event'])
  onScroll(event: Event): void {
    if (this.isNearBottom() && !this.loadingMorePosts) {
      this.bottomReached();
    }
    event.preventDefault();
  }

  private bottomReached(): void {
    this.loadingMorePosts = true;

    const loadMoreAction$ = of(this.store.dispatch(fromPostActions.loadMoreFeedPosts()));

    this.subscription.add(
      loadMoreAction$.subscribe(
        () => {
          if (loadMoreAction$ instanceof Observable) {
            this.subscription.add(
              loadMoreAction$
                .pipe(
                  finalize(() => {
                    this.loadingMorePosts = false;
                  }),
                )
                .subscribe(),
            );
          }
          this.loadingMorePosts = false;
        },
        (error) => {
          error.message;
          this.loadingMorePosts = false;
        },
      ),
    );
  }
}
